import React, {useState, useEffect} from 'react';
import ContainerHeader from 'components/ContainerHeader/index';
import {NotificationContainer, NotificationManager} from 'react-notifications';
import IntlMessages from 'util/IntlMessages';
import CardBox from 'components/CardBox/index';
// import Checkbox from '@material-ui/core/Checkbox';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import MenuItem from '@material-ui/core/MenuItem';
import FormControl from '@material-ui/core/FormControl';
import {base_url, public_url, $http} from "config";
import AttachFile from '@material-ui/icons/AttachFile';
import {useHistory} from 'react-router-dom';
import {useSelector} from 'react-redux';
import CircularProgress from '@material-ui/core/CircularProgress';
import Fab from '@material-ui/core/Fab';
import Radio from '@material-ui/core/Radio';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import RadioGroup from '@material-ui/core/RadioGroup';
import FormLabel from '@material-ui/core/FormLabel';

const ITEM_HEIGHT = 48;
const ITEM_PADDING_TOP = 8;

const RoomInfo = (props) => {

    const project_id = props.location.state ? props.location.state.project_id : null;
    const room_id = props.location.state ? props.location.state.id : null;
    const [loader, setLoader] = useState(false);

    const [project_name, setProjectName] = useState("");
    const [room_site, setRoomSite] = useState(0);
    const [room_project, setRoomProject] = useState(0);
    const [room_number, setRoomNumber] = useState("");
    const [room_height, setRoomHeight] = useState("");
    const [room_ceiling, setRoomCeiling] = useState("");
    const [room_wall, setRoomWall] = useState("");
    const [room_day, setRoomDay] = useState(0);
    const [room_hour, setRoomHour] = useState(0);
    const [room_img, setRoomImg] = useState([]);
    const [room_img_len, setRoomImgLen] = useState(0);
    const [room_img_url, setRoomImgUrl] = useState([]);
    const [room_asbestos, setRoomAsbestos] = useState("1");
    const [room_notes, setRoomNotes] = useState("");
    const [room_site_rows, setRoomSiteRows] = useState([]);
    const [room_departmentname_rows, setRoomDepartmentRows] = useState([]);
    const [room_buildingname_rows, setRoomBuildingRows] = useState([]);
    const [room_floorname_rows, setRoomFloorRows] = useState([]);
    const [room_department_id, setRoomDepartmentName] = useState("");
    const [room_building_id, setRoomBuildingName] = useState("");
    const [room_floor_id, setRoomFloorName] = useState("");
       

    const History = useHistory();
    const {width} = useSelector(({settings}) => settings);

    useEffect(() => {
        $http.get(base_url + "room/roomInfo", {params: {id: room_id, project_id: project_id}})
        .then((response) => {
            if (response.data) {
                setRoomSiteRows(response.data.sites)
                setRoomDepartmentRows(response.data.departments)
                setRoomBuildingRows(response.data.buildings)
                setRoomFloorRows(response.data.floors)
                var img_url = []
                if (room_id > 0) {
                    setRoomImg([]);
                    setRoomSite(response.data.room.site_id);
                    setRoomProject(response.data.room.project_id);
                    setRoomDepartmentName(response.data.room.department_id);
                    setRoomBuildingName(response.data.room.building_id);
                    setRoomFloorName(response.data.room.floor_id);
                    setRoomNumber(response.data.room.room_number);
                    setRoomDay(response.data.room.estimate_day);
                    setRoomHour(response.data.room.estimate_time);
                    setRoomHeight(response.data.room.ceiling_height);
                    setRoomCeiling(response.data.room.ceiling);
                    setRoomWall(response.data.room.wall);
                    setRoomAsbestos(response.data.room.asbestos);
                    setRoomNotes(response.data.room.notes);
                    setProjectName(response.data.room.project_name);
                    if(response.data.room.img_files.length > 0){
                        setRoomImgLen(response.data.room.img_files.length);
                        (response.data.room.img_files).map(url =>{
                            img_url.push(public_url + 'upload/img/' + url.img_name);
                        })
                    }
                    setRoomImgUrl(img_url);
                }
            }
            setLoader(true);
        })
    }, []);

    const removeImage=(item)=>{
        var img_url = [...room_img_url]
        img_url.splice(item, 1)
        setRoomImgUrl(img_url);
        if(item < room_img_len)
            setRoomImgLen(room_img_len-1);
        else{
            img_url = [...room_img]
            img_url.splice(item-room_img_len, 1)
            setRoomImg(img_url);
        }
    }

    const uploadMultipleFiles = (e) => {
        var fileObj = [];
        var img_url = [...room_img_url]
        fileObj.push(e.target.files);
        for (let i = 0; i < fileObj[0].length; i++) {
            img_url.push(URL.createObjectURL(fileObj[0][i]));
        }
        setRoomImgUrl(img_url);
        fileObj = [...room_img];
        for(let i = 0; i < e.target.files.length; i++) {
            fileObj.push(e.target.files[i]);
        }
        setRoomImg(fileObj);
    }

    const SaveRoom = (index) => {
        const frmData = new FormData();
        frmData.append('id', room_id);
        frmData.append('site_id', room_site);
        frmData.append('department_id', room_department_id);
        frmData.append('building_id', room_building_id);
        frmData.append('floor_id', room_floor_id);
        frmData.append('room_number', room_number);
        frmData.append('estimate_day', room_day);
        frmData.append('estimate_time', room_hour);
        frmData.append('ceiling_height', room_height);
        frmData.append('ceiling', room_ceiling);
        frmData.append('wall', room_wall);
        for (var i = 0; i < room_img.length; i++)
            frmData.append('room_img['+i+']', room_img[i]);
        frmData.append('img_array', room_img_url);
        frmData.append('asbestos', room_asbestos);
        frmData.append('notes', room_notes);
        frmData.append('img_index',index);
        if (project_id) {
            frmData.append('project_id', project_id);
            //frmData.append('customer_id', Project.company_id);
        }
        $http.post(base_url + "room/updateRoom", frmData)
        .then((response) => {
            if (response.data.status === "success") {
                NotificationManager.success(response.data.msg, "Success!", 5000, () => {
                });
                if(index >= 0) {
                    ModifyRoom(response.data.rooms,index)
                }
            } else if (response.data.status === "error") {
                NotificationManager.error(response.data.msg, "Error!", 5000, () => {
                });
            }
        }).catch((error)=>{
            console.log("error:", error);
        })
    }

    const ModifyRoom = (room_imgs,index) => {
        History.push({
            pathname: '/app/sitesurvey/live/sticker',
            state: {
                room_id: room_id,
                img_id: room_imgs[index].id,
                img_url: room_imgs[index].img_name,
            },
        })
    }

    return (
        <div className="app-wrapper">
            <ContainerHeader match={props.match} title="Room"/>
            { loader ? (
            <div className="row">
                <CardBox styleName="col-lg-12">
                    <form className="row" autoComplete="off">
                        <div className="col-md-6 col-12">
                            <TextField 
                                id="room_project"
                                label="Project"
                                // value={loader ? Project.project_name : ''}
                                value={project_name}
                                margin="normal"
                                helperText="Please Enter Project"
                                fullWidth
                                disabled
                                />
                        </div>
                        <div className="col-md-6 col-12">
                            <TextField
                                id="room_site"
                                select
                                label="Site"
                                value={room_site}
                                onChange={(event)=>setRoomSite(event.target.value)}
                                SelectProps={{}}
                                helperText="Please select Site"
                                margin="normal"
                                fullWidth
                            >
                                {room_site_rows.map(option => (
                                    <MenuItem key={option.id} value={option.id}>
                                        {option.site_name}
                                    </MenuItem>
                                ))}
                            </TextField>
                        </div>
                        <div className="col-md-6 col-12">
                            <TextField
                                id="room_department"
                                select
                                label="department"
                                value={room_department_id}
                                onChange={(event)=>setRoomDepartmentName(event.target.value)}
                                SelectProps={{}}
                                helperText="Please select Department"
                                margin="normal"
                                fullWidth
                            >
                                {room_departmentname_rows.filter(option => {
                                        return option.site_id == room_site
                                    }).map(option => (
                                    <MenuItem key={option.id} value={option.id}>
                                        {option.department_name}
                                    </MenuItem>
                                ))}
                            </TextField>
                        </div>
                        <div className="col-md-6 col-12">
                            <TextField
                                id="room_building"
                                select
                                label="building"
                                value={room_building_id}
                                onChange={(event)=>setRoomBuildingName(event.target.value)}
                                SelectProps={{}}
                                helperText="Please select Building"
                                margin="normal"
                                fullWidth
                            >
                                {room_buildingname_rows.filter(option => {
                                        return option.department_id == room_department_id
                                    }).map(option => (
                                    <MenuItem key={option.id} value={option.id}>
                                        {option.building_name}
                                    </MenuItem>
                                ))}
                            </TextField>
                        </div>
                        <div className="col-md-6 col-12">
                            <TextField
                                id="room_floor"
                                select
                                label="floor"
                                value={room_floor_id}
                                onChange={(event)=>setRoomFloorName(event.target.value)}
                                SelectProps={{}}
                                helperText="Please select Floor"
                                margin="normal"
                                fullWidth
                            >
                                {room_floorname_rows.filter(option => {
                                        return option.building_id == room_building_id
                                    }).map(option => (
                                    <MenuItem key={option.id} value={option.id}>
                                        {option.floor_name}
                                    </MenuItem>
                                ))}
                            </TextField>
                        </div>
                        <div className="col-md-6 col-12">
                            <TextField 
                                id="room_number"
                                label="Room Number"
                                value={room_number}
                                onChange={(event)=>setRoomNumber(event.target.value)}
                                margin="normal"
                                helperText="Please Enter Room Number"
                                fullWidth
                                />
                        </div>
                        <div className="col-md-6 col-12">
                            <TextField 
                                id="room_height"
                                label="Ceiling Heights"
                                value={room_height}
                                onChange={(event)=>setRoomHeight(event.target.value)}
                                margin="normal"
                                helperText="Please Enter Ceiling Heights"
                                fullWidth
                                />
                        </div>
                        <div className="col-md-6 col-12">
                            <TextField 
                                id="room_ceiling"
                                label="Ceiling"
                                value={room_ceiling}
                                onChange={(event)=>setRoomCeiling(event.target.value)}
                                margin="normal"
                                helperText="Please Enter Ceiling"
                                fullWidth
                                />
                        </div>
                        <div className="col-md-6 col-12">
                            <TextField 
                                id="room_wall"
                                label="Wall"
                                value={room_wall}
                                onChange={(event)=>setRoomWall(event.target.value)}
                                margin="normal"
                                helperText="Please Enter Wall"
                                fullWidth
                                />
                        </div>
                        <div className="col-md-6 col-12">
                            <TextField
                                id="room_day"
                                select
                                label="Installation Estimate Days"
                                value={room_day}
                                onChange={(event)=>setRoomDay(event.target.value)}
                                SelectProps={{}}
                                helperText="Please select Days"
                                margin="normal"
                                fullWidth
                            >
                                {[...Array(24)].map((id, value) => (
                                    <MenuItem key={value} value={value}>
                                        {value}
                                    </MenuItem>
                                ))}
                            </TextField>
                        </div>
                        <div className="col-md-6 col-12">
                            <TextField
                                id="room_hour"
                                select
                                label="Installation Estimate Hours"
                                value={room_hour}
                                onChange={(event)=>setRoomHour(event.target.value)}
                                SelectProps={{}}
                                helperText="Please select Days"
                                margin="normal"
                                fullWidth
                            >
                                {[...Array(10)].map((id, value) => (
                                    <MenuItem key={value} value={value}>
                                        {value}
                                    </MenuItem>
                                ))}
                            </TextField>
                        </div>
                        <div className="col-md-6 col-12 d-flex align-items-center">
                            
                            <FormControl component="fieldset" required>
                                <FormLabel component="legend">Asbestos</FormLabel>
                                <RadioGroup
                                className="d-flex flex-row"
                                aria-label="gender"
                                name="gender"
                                value={room_asbestos}
                                onChange={(event, value) => setRoomAsbestos(value)}
                                >
                                <FormControlLabel value="1" control={<Radio color="primary"/>} label="Yes"/>
                                <FormControlLabel value="0" control={<Radio color="primary"/>} label="No"/>
                                </RadioGroup>
                            </FormControl>
                        </div>
                        <div className="col-md-6 col-12">
                            <TextField
                                id="room_notes"
                                label="Notes"
                                multiline
                                rowsMax="4"
                                value={room_notes}
                                onChange={(event) => setRoomNotes(event.target.value)}
                                margin="normal"
                                fullWidth
                            />
                        </div>
                        <div className="col-md-12 col-12 my-4 text-center">
                            <input type="file" id="room_img" accept="image/*" multiple 
                                name="room_img[]" style={{display: "none"}}
                                onChange={(event) => {
                                    uploadMultipleFiles(event);
                                    //setRoomImg(event.target.files[0]);
                                }}/>
                            <Button className="jr-btn" color="primary"
                                variant="contained" onClick={(e)=>{document.getElementById('room_img').click()}}>
                                <AttachFile/> Add Image
                            </Button>
                        </div>
                            {room_img_url.map( (url,index) => 
                                <div className="col-xl-3 col-lg-4 col-md-4 col-6" key={index}>
                                <div className="jr-card pb-2">
                                    <div className="jr-card-thumb">
                                        <img className="card-img-top img-fluid" alt="products" src={url}/>
                                    </div>
                                    
                                    <div className="jr-card-social text-center">
                                        <Fab className="jr-fab-btn bg-light-blue accent-2 text-white" 
                                            onClick = {() => SaveRoom(index)}>
                                            <i className="zmdi zmdi-puzzle-piece zmdi-hc-2x"/>
                                        </Fab>
                                        <Fab className="jr-fab-btn bg-light-blue accent-2 text-white"  onClick = {()=>removeImage(index)}>
                                            <i className="zmdi zmdi-delete zmdi-hc-2x"/>
                                        </Fab>
                                    </div>
                                </div>
                                </div>
                            )}
                        <div className="col-md-12 col-12 text-right">
                            <Button variant="contained" color="primary" className="jr-btn" 
                                onClick={() => SaveRoom()}>
                                Save
                            </Button>
                        </div>
                    </form>
                </CardBox>
            </div>
            ):(
                <div className="loader-view"
                        style={{height: width >= 1200 ? 'calc(100vh - 259px)' : 'calc(100vh - 238px)'}}>
                    <CircularProgress/>
                </div>
            )}
    
            <NotificationContainer/>
        </div>
    );
}

export default RoomInfo;